<template>
    <div>
        <p>The visited URL is {{url}}</p>
        <button @click="sayHello">Hello </button>
    </div>
</template>
<script>
export default {
    data: {
        url: 'Lost it',
    },
    methods: {
        sayHello() {
            window.alert('hello!');
        }
    }
}
</script>


